/*
 *
 * DeploymentItemLevelViewWrapper actions
 *
 */

import { BaseAction } from 'cb-utils/baseAction';
import { ActionConstants } from './actionConstants';
import { EntityModels } from '../Console/state';

export interface InitItemLevelwrapperRequest extends BaseAction {
  type: ActionConstants.INIT_ITEM_LEVEL_WRAPPER_REQUEST;
  payload: {
    systemKey: string;
    deploymentId: string;
    assetClass: keyof EntityModels;
  };
}

export const createInitItemLevelwrapperRequest = (
  systemKey: string,
  deploymentId: string,
  assetClass: keyof EntityModels,
): InitItemLevelwrapperRequest => ({
  type: ActionConstants.INIT_ITEM_LEVEL_WRAPPER_REQUEST,
  payload: {
    deploymentId,
    systemKey,
    assetClass,
  },
});

export interface InitItemLevelwrapperSuccess extends BaseAction {
  type: ActionConstants.INIT_ITEM_LEVEL_WRAPPER_SUCCESS;
}

export const createInitItemLevelwrapperSuccess = (): InitItemLevelwrapperSuccess => ({
  type: ActionConstants.INIT_ITEM_LEVEL_WRAPPER_SUCCESS,
});

export type ActionTypes =
  | InitItemLevelwrapperRequest
  | InitItemLevelwrapperSuccess;



