/*
 *
 * DeploymentItemLevelView
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { Link } from 'react-router-dom';
import { push } from 'react-router-redux';
import { selectAssetClass } from 'containers/Console/consoleSelectors';
import * as ducks from 'containers/Console/ducks';
import styled from 'styled-components';

import { Container, Button } from 'reactstrap';

import messages from './messages';
import { FormattedMessage, injectIntl } from 'react-intl';
import ConsolePage from 'components/ConsolePage';
import PageHeader from 'components/ConsolePageHeader';
import PageFooter from 'components/ConsolePageFooter';
import Helmet from 'components/DeployHelmet';
import DeploymentItemViewHeader from 'components/DeploymentModalItemViewHeader';
import BackIcon from 'react-icons/lib/fa/long-arrow-left';
import { getSliceName, GenericDuckSlicesEnum } from 'containers/Console/state';
import { AssetConfig } from 'cb-utils/console-entity-models/deployment';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';
import { selectCurrentItems, selectCurrentEdges } from './selectors';
import injectReducer from 'utils/injectReducer';
import injectSaga from 'utils/injectSaga';
import { STORE_KEY } from './constants';
import reducer from './reducer';
import saga from './sagas';
import {
  createInitItemLevelViewRequest,
  createOnItemChange,
  createOnEdgeChange,
} from './actions';
import { DeploymentItemLevelRouteProps } from '../Console/routes/deployments';
import {
  createAssetChange,
  createApplyGridEdges,
} from '../DeployDetail/actions';
import { CBDispatch } from 'cb-utils/typeUtils';
import { shouldDeployToAllEdges, AnyMap } from 'cb-utils/console-entity-models';
import DeploymentItemLevelGrid from 'components/DeploymentItemLevelGrid';
import DeploymentEdgeGrid from 'components/DeploymentEdgeGrid';
import { AppState } from 'reducers';
import { createStructuredSelector } from 'reselect';

const ContainerWrapper = styled(Container)`
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
`;

const GridWrapper = styled.div`
  height: 100%;
  display: flex;
`;

const getAssetClass = (props: DeploymentItemLevelViewProps) =>
  props.match.params.assetClass;

const getPrevUrl = (props: DeploymentItemLevelViewProps) => {
  const split = props.match.url.split('/');
  return split.slice(0, split.length - 1).join('/');
};

interface DeploymentItemLevelViewProps
  extends SystemRouteProps<DeploymentItemLevelRouteProps> {
  readOnly?: boolean; // todo - implement this
}

class DeploymentItemLevelView extends React.PureComponent<
  DeploymentItemLevelViewProps &
    StateToProps &
    DispatchToProps &
    ReactIntl.InjectedIntlProps,
  {}
> {
  assetClass = getAssetClass(this.props);
  prevUrl = getPrevUrl(this.props);

  componentWillMount() {
    this.props.init();
  }

  render() {
    const {
      selectedItems,
      entity,
      rows,
      onChangeItems,
      customEdgeColumns,
      selectedEdges,
      onChangeEdges,
      match: { params: { systemKey, assetClass } },
    } = this.props;
    const { collectionName, sliceName } = getSliceName(assetClass);
    return (
      <React.Fragment>
        <Helmet />
        <ConsolePage subheader={this._renderHeader()}>
          <ContainerWrapper fluid>
            <GridWrapper>
              {this.assetClass === GenericDuckSlicesEnum.edges ? (
                <DeploymentEdgeGrid
                  selectedEdges={selectedEdges}
                  onToggleEdges={onChangeEdges}
                  columnNames={[
                    'name',
                    'isConnected',
                    'last_talked',
                    'description',
                  ].concat(Object.keys(customEdgeColumns))}
                />
              ) : (
                <DeploymentItemLevelGrid
                  selected={selectedItems}
                  sliceName={sliceName}
                  assetClass={this.assetClass}
                  collectionName={collectionName}
                  systemKey={systemKey}
                  entity={entity}
                  rows={rows as AnyMap[]}
                  onChange={onChangeItems}
                />
              )}
            </GridWrapper>
          </ContainerWrapper>
        </ConsolePage>
        <PageFooter
          actionButtons={() => (
            <React.Fragment>
              {this._renderCancelButton()}
              {this._renderSubmitButton()}
            </React.Fragment>
          )}
        />
      </React.Fragment>
    );
  }

  _renderCancelButton = () => {
    return (
      <Link to={this.prevUrl}>
        <Button color="default">
          <BackIcon />&nbsp;
          <FormattedMessage {...messages.backButtonText} />{' '}
        </Button>
      </Link>
    );
  };

  _renderSubmitButton = () => {
    const {
      selectedItems,
      selectedEdges,
      intl: { formatMessage },
    } = this.props;
    if (this.assetClass === GenericDuckSlicesEnum.edges) {
      return (
        <Button
          data-cy="apply-selected-items"
          color="primary"
          disabled={
            selectedEdges.length === 0 || shouldDeployToAllEdges(selectedEdges)
          }
          onClick={() => this.props.updateEdgesForDeployment(selectedEdges)}
        >
          {shouldDeployToAllEdges(selectedEdges) ? (
            <FormattedMessage {...messages.allEdgesSelected} />
          ) : (
            formatMessage(messages.applyItems, { number: selectedEdges.length })
          )}
        </Button>
      );
    }
    return (
      <Button
        data-cy="apply-selected-items"
        color="primary"
        disabled={selectedItems.length === 0}
        onClick={() => this.props.updateItemsForAssetClass(selectedItems)}
      >
        {formatMessage(messages.applyItems, { number: selectedItems.length })}
      </Button>
    );
  };

  _renderHeader = () => {
    return <PageHeader header={this._renderHeaderContents()} />;
  };

  _renderHeaderContents = () => {
    return <DeploymentItemViewHeader assetClass={this.assetClass} />;
  };
}

type StateToProps = ReturnType<ReturnType<typeof mapStateToProps>>;

const mapStateToProps = (
  state: AppState,
  ownProps: DeploymentItemLevelViewProps,
) => {
  const assetClass = getAssetClass(ownProps);
  const { sliceName, collectionName } = getSliceName(assetClass);
  const slice = ducks[sliceName];
  return createStructuredSelector({
    entity: selectAssetClass(assetClass),
    rows: slice.selectors.rows(collectionName),
    selectedItems: selectCurrentItems(),
    selectedEdges: selectCurrentEdges(),
    customEdgeColumns: ducks.edgeSelectors.customColumns(),
  });
};

type DispatchToProps = ReturnType<typeof mapDispatchToProps>;
function mapDispatchToProps(
  dispatch: CBDispatch,
  ownProps: DeploymentItemLevelViewProps,
) {
  const assetClass = getAssetClass(ownProps);
  const depName = ownProps.match.params.deploymentName;
  return {
    init: () =>
      dispatch(
        createInitItemLevelViewRequest(
          ownProps.match.params.systemKey,
          depName,
          assetClass,
        ),
      ),
    onChangeItems: (assets: AssetConfig[]) =>
      dispatch(createOnItemChange(assets)),
    updateItemsForAssetClass: (assets: AssetConfig[]) => {
      dispatch(createAssetChange(depName, assetClass, assets));
      dispatch(push(getPrevUrl(ownProps)));
    },
    onChangeEdges: (edges: string[]) => dispatch(createOnEdgeChange(edges)),
    updateEdgesForDeployment: (edges: string[]) => {
      dispatch(createApplyGridEdges(depName, edges));
      dispatch(push(getPrevUrl(ownProps)));
    },
  };
}

const withConnect = connect<
  StateToProps,
  DispatchToProps,
  DeploymentItemLevelViewProps
>(mapStateToProps, mapDispatchToProps);

const withReducer = injectReducer({ key: STORE_KEY, reducer });
const withSaga = injectSaga({ key: STORE_KEY, saga });

export default compose(withReducer, withSaga, withConnect, injectIntl)(
  DeploymentItemLevelView,
) as React.ComponentType<DeploymentItemLevelViewProps>;



