import { all, takeEvery, put, select } from 'redux-saga/effects';
import { ActionConstants } from './actionConstants';
import { ActionConstants as DeploymentActionConstants } from 'containers/Console/actions/deploymentConstants';
import {
  InitItemLevelViewRequest,
  createFetchedAssets,
  createOnEdgeChange,
} from './actions';
import { SetModifiableDeployment } from 'containers/Console/actions/deployment';
import {
  fetchSingleDeploymentSaga,
  fetchSingleDeploymentErrorSaga,
  setModifiableDeploymentSaga,
  fetchDeploymentIfNecessary,
} from 'containers/Console/sagas/deployments';
import { AssetConfig } from 'cb-utils/console-entity-models';
import { GenericDuckSlicesEnum } from 'containers/Console/state';
import { selectModifiableDeploymentEdges } from 'containers/Console/selectors/deployments';

export function* initAssetManagerSaga() {
  yield takeEvery(ActionConstants.INIT_ITEM_LEVEL_VIEW_REQUEST, function*(
    action: InitItemLevelViewRequest,
  ) {
    const currentDeployment = yield fetchDeploymentIfNecessary(
      action.payload.systemKey,
      action.payload.deploymentId,
    );
    if (action.payload.assetClass === GenericDuckSlicesEnum.edges) {
      const selectedEdges = yield select(
        selectModifiableDeploymentEdges(action.payload.deploymentId),
      );
      yield put(createOnEdgeChange(selectedEdges));
    }
    if (currentDeployment) {
      yield setAssets(currentDeployment.assets);
    }
  });
}

function* setAssets(assets: AssetConfig[]) {
  yield put(createFetchedAssets(assets));
}

function* deploymentSetSaga() {
  yield takeEvery(
    DeploymentActionConstants.SET_MODIFIABLE_DEPLOYMENT,
    function*(action: SetModifiableDeployment) {
      yield setAssets(action.payload.config.assets);
    },
  );
}

// All sagas to be loaded
export default function*() {
  yield all([
    initAssetManagerSaga(),
    fetchSingleDeploymentSaga(),
    fetchSingleDeploymentErrorSaga(),
    setModifiableDeploymentSaga(),
    deploymentSetSaga(),
  ]);
}



