/**
 *
 * DeploymentModalItemViewHeader
 *
 */

import React, { SFC } from 'react';
import { EntityModels } from 'containers/Console/state';
// import styled from 'styled-components';

import { DevicesIcon, DatabaseIcon, UserIcon } from 'components/EntityIcons';

interface DeploymentModalItemViewHeaderProps {
  assetClass: keyof EntityModels;
}

const renderAssetClassIcon = (assetClass: keyof EntityModels) => {
  switch (assetClass) {
    case 'devices':
      return <DevicesIcon />;
    case 'users':
      return <UserIcon />;
    default:
      return <DatabaseIcon />;
  }
};

const renderAssetClassTitle = (assetClass: keyof EntityModels) => {
  switch (assetClass) {
    case 'devices':
      return 'Select Devices';
    case 'users':
      return 'Select Users';
    case 'edges':
      return 'Select Edges';
    default:
      return `Select items from ${assetClass}`;
  }
};

const DeploymentModalItemViewHeader: SFC<DeploymentModalItemViewHeaderProps> = (
  props,
) => {
  return (
    <div>
      {renderAssetClassIcon(props.assetClass)}{' '}
      {renderAssetClassTitle(props.assetClass)}
    </div>
  );
};

export default DeploymentModalItemViewHeader;



