/*
 *
 * DeploymentItemLevelViewWrapper
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import injectSaga from 'utils/injectSaga';
import { compose } from 'redux';
import injectReducer from 'utils/injectReducer';
import makeSelectDeploymentItemLevelViewWrapper from './selectors';
import reducer from './reducer';
import { STORE_KEY } from './constants';
import saga from './sagas';
import ItemLevelView from 'containers/DeploymentItemLevelView';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';
import { DeploymentItemLevelRouteProps } from '../Console/routes/deployments';
import { createInitItemLevelwrapperRequest } from './actions';
import LoadingIndicator from 'components/LoadingIndicator';
import { CBDispatch } from 'cb-utils/typeUtils';
import { createStructuredSelector } from 'reselect';

interface DeploymentItemLevelViewWrapperProps
  extends SystemRouteProps<DeploymentItemLevelRouteProps> {}

export class DeploymentItemLevelViewWrapper extends React.PureComponent<
  DeploymentItemLevelViewWrapperProps & StateToProps & DispatchToProps,
  {}
> {
  componentWillMount() {
    this.props.init();
  }

  render() {
    const { state: { hasLoaded }, match } = this.props;
    if (hasLoaded) {
      return <ItemLevelView match={match} location={null} history={null} />;
    }
    return <LoadingIndicator />;
  }
}

type StateToProps = ReturnType<typeof mapStateToProps>;

const mapStateToProps = createStructuredSelector({
  state: makeSelectDeploymentItemLevelViewWrapper(),
});

type DispatchToProps = ReturnType<typeof mapDispatchToProps>;
function mapDispatchToProps(
  dispatch: CBDispatch,
  ownProps: DeploymentItemLevelViewWrapperProps,
) {
  return {
    init: () =>
      dispatch(
        createInitItemLevelwrapperRequest(
          ownProps.match.params.systemKey,
          ownProps.match.params.deploymentName,
          ownProps.match.params.assetClass,
        ),
      ),
  };
}

const withConnect = connect(mapStateToProps, mapDispatchToProps);

const withReducer = injectReducer({ key: STORE_KEY, reducer });
const withSaga = injectSaga({ key: STORE_KEY, saga });
export default compose(withReducer, withSaga, withConnect)(
  DeploymentItemLevelViewWrapper,
);



