/**
 *
 * DeploymentItemLevelGrid
 *
 */

import React from 'react';
// import styled from 'styled-components';

import GridContainer from 'containers/ConsoleCrudgridContainer/Loadable';
import { GenericDuckSlices, TableEntityModel } from 'containers/Console/state';
import {
  AssetConfig,
  doesAssetExist,
  AnyMap,
} from 'cb-utils/console-entity-models';
import { createCheckBoxCell } from 'cb-utils/createCheckBoxCell';
import { Column, RowInfo } from 'react-table';
import consoleMessages from 'containers/Console/messages';
import { findIndex } from 'lodash';

const pagesMatch = (
  pk: string,
  selected: AssetConfig[],
  rows: AnyMap[],
): boolean => {
  if (!rows) {
    return false;
  }
  for (let i = 0, len = rows.length; i < len; i += 1) {
    if (!isRowInSelectedList(selected, rows[i][pk])) {
      return false;
    }
  }
  return true;
};

const isRowInSelectedList = (
  selected: AssetConfig[],
  assetId: string,
  extraProps = {},
) => {
  return findIndex(selected, { ...extraProps, asset_id: assetId }) > -1;
};

interface DeploymentItemLevelGridProps {
  assetClass: string;
  sliceName: GenericDuckSlices;
  systemKey: string;
  selected: AssetConfig[];
  collectionName: string;
  entity: TableEntityModel;
  rows: AnyMap[];
  onChange: (newSelected: AssetConfig[]) => void;
}

class DeploymentItemLevelGrid extends React.PureComponent<
  DeploymentItemLevelGridProps,
  {}
> {
  render() {
    const { selected, sliceName, collectionName, systemKey } = this.props;
    return (
      <GridContainer
        selected={selected}
        assetClass={sliceName}
        collectionName={collectionName}
        systemKey={systemKey}
        transformColumns={this._transformColumns}
      />
    );
  }

  _hasSelectedAll = (): boolean => {
    return pagesMatch(
      this.props.entity.PKColumnName,
      this.props.selected,
      this.props.rows,
    );
  };

  _transformColumns = (cols: Column[]): Column[] => {
    return [
      {
        Header: () => (
          <div data-cy="toggle-deploy-all">
            {createCheckBoxCell(
              this._toggleDeployAll,
              this._hasSelectedAll(),
              consoleMessages.deploy,
            )}
          </div>
        ),
        filterable: false,
        resizable: false,
        sortable: false,
        width: 80,
        Cell: (row) => (
          <div>
            {createCheckBoxCell(
              (isChecked) => this._toggleItemDeploy(isChecked, row),
              this._isItemDeployed(row),
            )}
          </div>
        ),
      },
      {
        Header: () => (
          <div>
            {createCheckBoxCell(
              this._toggleSyncAll,
              this._areAllItemsSynced(),
              consoleMessages.sync,
            )}
          </div>
        ),
        filterable: false,
        resizable: false,
        sortable: false,
        width: 70,
        Cell: (row) => (
          <div>
            {createCheckBoxCell(
              (isChecked) => this._toggleItemSync(isChecked, row),
              this._isItemSynced(row),
            )}
          </div>
        ),
      },
      ...cols,
    ];
  };

  _toggleDeployAll = (isChecked: boolean) => {
    let newSelected: AssetConfig[];
    if (isChecked) {
      newSelected = this.props.rows
        .filter(
          // we need to filter out any rows that are already selected
          (s) =>
            findIndex(this.props.selected, {
              asset_id: s[this.props.entity.PKColumnName],
            }) === -1,
        )
        .map((d): AssetConfig => {
          return {
            asset_class: this.props.assetClass,
            asset_id: d[this.props.entity.PKColumnName],
            sync: true,
          };
        })
        .concat(this.props.selected); // we need to concat with what's already selected to allow for selections across pages
    } else {
      newSelected = this.props.selected.filter((s) => {
        return (
          findIndex(this.props.rows, {
            [this.props.entity.PKColumnName]: s.asset_id,
          }) === -1
        );
      });
    }
    this._setSelected(newSelected);
  };

  _isItemDeployed = (rowInfo: RowInfo): boolean => {
    return doesAssetExist(
      this.props.selected,
      rowInfo.row[this.props.entity.PKColumnName],
    );
  };

  _toggleItemDeploy = (isChecked: boolean, row: RowInfo) => {
    let newSelected = [...this.props.selected];
    if (isChecked) {
      newSelected.push({
        asset_class: this.props.assetClass,
        asset_id: row.row[this.props.entity.PKColumnName],
        sync: true,
      });
    } else {
      newSelected = this.props.selected.filter(
        (s) => s.asset_id !== row.row[this.props.entity.PKColumnName],
      );
    }
    this._setSelected(newSelected);
  };

  _toggleSyncAll = (isChecked: boolean) => {
    if (!this._hasSelectedAll()) {
      // don't do anything if user hasn't set deployAll
      return;
    }
    const newSelected = this.props.selected.map((s) => {
      if (
        findIndex(this.props.rows, {
          [this.props.entity.PKColumnName]: s.asset_id,
        }) > -1
      ) {
        return {
          ...s,
          sync: isChecked,
        };
      }
      return s;
    });
    this._setSelected(newSelected);
  };

  _areAllItemsSynced = () => {
    if (!this._hasSelectedAll()) {
      return false;
    }
    for (let i = 0, len = this.props.rows.length; i < len; i += 1) {
      if (
        !isRowInSelectedList(
          this.props.selected,
          this.props.rows[i][this.props.entity.PKColumnName],
          { sync: true },
        )
      ) {
        return false;
      }
    }
    return true;
  };

  _isItemSynced = (row: RowInfo) => {
    return doesAssetExist(
      this.props.selected,
      row.row[this.props.entity.PKColumnName],
      { sync: true },
    );
  };

  _toggleItemSync = (isChecked: boolean, row: RowInfo) => {
    const newSelected = this.props.selected.map((s) => {
      if (s.asset_id === row.row[this.props.entity.PKColumnName]) {
        return {
          ...s,
          sync: isChecked,
        };
      }
      return s;
    });
    this._setSelected(newSelected);
  };

  _setSelected = (newSelected: AssetConfig[]) => {
    this.props.onChange(newSelected);
  };
}

export default DeploymentItemLevelGrid;



