import { all, takeEvery, put, select } from 'redux-saga/effects';
import { ActionConstants } from './actionConstants';
import {
  createInitItemLevelwrapperSuccess,
  InitItemLevelwrapperRequest,
} from './actions';
import { selectEntities } from 'containers/Console/consoleSelectors';
import * as ducks from 'containers/Console/ducks';
import { EntityModels } from 'containers/Console/state';

function* callInitSuccess() {
  yield put(createInitItemLevelwrapperSuccess());
}

function* collectionsLoadedSaga() {
  yield takeEvery(
    ducks.collections.actions.fetchAll.successType,
    callInitSuccess,
  );
}

function* initSaga() {
  yield takeEvery(ActionConstants.INIT_ITEM_LEVEL_WRAPPER_REQUEST, function*(
    action: InitItemLevelwrapperRequest,
  ) {
    switch (action.payload.assetClass) {
      case 'devices':
      case 'users':
        yield callInitSuccess();
        return;
      default:
        const entities: EntityModels = yield select(selectEntities());
        if (!entities.collections.hasFetched) {
          yield put(
            ducks.collections.actions.fetchAll.request(
              action.payload.systemKey,
            ),
          );
        } else {
          yield callInitSuccess();
        }
    }
  });
}

// All sagas to be loaded
export default function*() {
  yield all([initSaga(), collectionsLoadedSaga()]);
}



