import { createSelector } from 'reselect';
import { STORE_KEY } from './constants';
import { State } from './reducer';
import { AppState } from 'reducers';

/**
 * Direct selector to the deploymentItemLevelViewWrapper state domain
 */
const selectDeploymentItemLevelViewWrapperDomain = () => (
  state: AppState,
): State => state[STORE_KEY];

/**
 * Other specific selectors
 */

/**
 * Default selector used by DeploymentItemLevelViewWrapper
 */

const makeSelectDeploymentItemLevelViewWrapper = () =>
  createSelector(
    selectDeploymentItemLevelViewWrapperDomain(),
    (substate) => substate,
  );

export default makeSelectDeploymentItemLevelViewWrapper;
export { selectDeploymentItemLevelViewWrapperDomain };



