/*
 *
 * DeploymentItemLevelViewWrapper reducer
 *
 */

import assign from 'immutable-assign';
import { ActionTypes } from './actions';
import { ActionConstants } from './actionConstants';

export interface State {
  hasLoaded: boolean;
}

export const initialState: State = {
  hasLoaded: false,
};

function deploymentItemLevelViewWrapperReducer(
  state: State = initialState,
  action: ActionTypes,
) {
  switch (action.type) {
    case ActionConstants.INIT_ITEM_LEVEL_WRAPPER_SUCCESS:
      return assign(state, (s) => s.hasLoaded, () => true);
    default:
      return state;
  }
}

export default deploymentItemLevelViewWrapperReducer;



