/*
 *
 * DeploymentItemLevelView reducer
 *
 */

import assign from 'immutable-assign';
import { ActionTypes } from './actions';
import { ActionConstants } from './actionConstants';
import {
  AssetConfig,
  SpecialDeploymentKeys,
} from 'cb-utils/console-entity-models';
import { EntityModels } from '../Console/state';

export interface State {
  assetClass: keyof EntityModels;
  selectedItems: AssetConfig[];
  selectedEdges: string[];
}

export const initialState: State = {
  assetClass: null,
  selectedItems: [],
  selectedEdges: [],
};

function deploymentItemLevelViewReducer(
  state: State = initialState,
  action: ActionTypes,
) {
  switch (action.type) {
    case ActionConstants.INIT_ITEM_LEVEL_VIEW_REQUEST:
      return assign(
        state,
        (s) => s.assetClass,
        () => action.payload.assetClass,
      );
    case ActionConstants.FETCHED_ASSETS:
      return assign(state, (s) => {
        s.selectedItems = action.payload.assets.filter(
          (a) =>
            a.asset_class === s.assetClass &&
            a.asset_id !== SpecialDeploymentKeys.ALL_ASSETS_IN_CLASS,
        );
        return s;
      });
    case ActionConstants.ON_ITEM_CHANGE:
      return assign(state, (s) => s.selectedItems, () => action.payload.assets);
    case ActionConstants.ON_EDGE_CHANGE:
      return assign(state, (s) => s.selectedEdges, () => action.payload.edges);
    default:
      return state;
  }
}

export default deploymentItemLevelViewReducer;



