import { createSelector } from 'reselect';
import { STORE_KEY } from './constants';
import { State } from './reducer';
import { AppState } from 'reducers';

/**
 * Direct selector to the deploymentItemLevelView state domain
 */
const selectDeploymentItemLevelViewDomain = () => (state: AppState): State =>
  state[STORE_KEY];

/**
 * Other specific selectors
 */

/**
 * Default selector used by DeploymentItemLevelView
 */

const makeSelectDeploymentItemLevelView = () =>
  createSelector(selectDeploymentItemLevelViewDomain(), (substate) => substate);

const selectCurrentItems = () =>
  createSelector(
    makeSelectDeploymentItemLevelView(),
    (substate) => substate.selectedItems,
  );

const selectCurrentEdges = () =>
  createSelector(
    makeSelectDeploymentItemLevelView(),
    (substate) => substate.selectedEdges,
  );

export default makeSelectDeploymentItemLevelView;
export {
  selectDeploymentItemLevelViewDomain,
  selectCurrentItems,
  selectCurrentEdges,
};



