/*
 *
 * DeploymentItemLevelView actions
 *
 */

import { BaseAction } from 'cb-utils/baseAction';
import { ActionConstants } from './actionConstants';
import { AssetConfig } from 'cb-utils/console-entity-models';
import { EntityModels } from '../Console/state';

export interface InitItemLevelViewRequest extends BaseAction {
  type: ActionConstants.INIT_ITEM_LEVEL_VIEW_REQUEST;
  payload: {
    systemKey: string;
    deploymentId: string;
    assetClass: keyof EntityModels;
  };
}

export const createInitItemLevelViewRequest = (
  systemKey: string,
  deploymentId: string,
  assetClass: keyof EntityModels,
): InitItemLevelViewRequest => ({
  type: ActionConstants.INIT_ITEM_LEVEL_VIEW_REQUEST,
  payload: {
    deploymentId,
    systemKey,
    assetClass,
  },
});

export interface FetchedAssets extends BaseAction {
  type: ActionConstants.FETCHED_ASSETS;
  payload: {
    assets: AssetConfig[];
  };
}

export const createFetchedAssets = (assets: AssetConfig[]): FetchedAssets => ({
  type: ActionConstants.FETCHED_ASSETS,
  payload: {
    assets,
  },
});

export interface OnItemChange extends BaseAction {
  type: ActionConstants.ON_ITEM_CHANGE;
  payload: {
    assets: AssetConfig[];
  };
}

export const createOnItemChange = (assets: AssetConfig[]): OnItemChange => ({
  type: ActionConstants.ON_ITEM_CHANGE,
  payload: {
    assets,
  },
});

export interface OnEdgeChange extends BaseAction {
  type: ActionConstants.ON_EDGE_CHANGE;
  payload: {
    edges: string[];
  };
}

export const createOnEdgeChange = (edges: string[]): OnEdgeChange => ({
  type: ActionConstants.ON_EDGE_CHANGE,
  payload: {
    edges,
  },
});

export type ActionTypes =
  | OnItemChange
  | OnEdgeChange
  | FetchedAssets
  | InitItemLevelViewRequest;



